.mine-details .record-goback {
  height: 18px;
  font-size: 14px;
  color: #0091ff;
  line-height: 18px;
  cursor: pointer;
  margin-bottom: 20px;
}
.mine-details .mine-body {
  width: 853px;
  background: #ffffff;
  box-shadow: 0px 0px 20px 0px rgba(246, 246, 246, 0.78);
  border-radius: 8px;
  border: 1px solid rgba(0, 0, 0, 0.06);
  padding: 30px;
}
.mine-details .mine-body .mine-heder {
  font-size: 14px;
  font-weight: 400;
  color: rgba(0, 0, 0, 0.85);
  line-height: 16px;
}
.mine-details .mine-body .mine-header-con {
  width: 304px;
  font-size: 12px;
  font-weight: 400;
  color: rgba(0, 0, 0, 0.5);
  line-height: 16px;
  padding-top: 20px;
}
.mine-details .mine-body .serial-name {
  height: 17px;
  font-size: 14px;
  font-weight: 400;
  color: rgba(0, 0, 0, 0.85);
  line-height: 16px;
  margin-top: 20px;
}
.mine-details .mine-body .serial-num {
  height: 17px;
  font-size: 12px;
  font-weight: 400;
  color: rgba(0, 0, 0, 0.25);
  line-height: 16px;
  margin-bottom: 45px;
}
.mine-details .mine-body .mine-back {
  height: 15px;
  padding: 0 3px;
  background: #ffd232;
  border-radius: 4px;
  font-size: 10px;
  font-weight: 400;
  color: rgba(255, 255, 255, 0.85);
  line-height: 15px;
  margin-left: 5px;
  text-align: center;
  display: inline-block;
}
.mine-details .mine-body .mine-balance {
  width: 781px;
  height: 113px;
  background: #f5f5f5;
  border-radius: 4px;
  margin-bottom: 30px;
}
.mine-details .mine-body .mine-name {
  font-size: 12px;
  font-weight: 500;
  color: rgba(0, 0, 0, 0.5);
  line-height: 16px;
  margin-top: 30px;
  text-align: center;
}
.mine-details .mine-body .mine-num {
  font-size: 19px;
  font-weight: 500;
  color: rgba(0, 0, 0, 0.65);
  line-height: 19px;
  margin-bottom: 35px;
  text-align: center;
}
.mine-details .mine-body .node-list {
  width: 781px;
  height: 55px;
  line-height: 55px;
  background: #f5f5f5;
  border-radius: 4px;
  margin-top: 8px;
  padding-left: 28px;
}
.mine-details .mine-body .min-btns {
  float: right;
}
@media only screen and (max-width: 768px) {
  .mine-details .mine-body {
    width: 100%;
    padding: 10px;
  }
  .mine-details .mine-body .mine-balance {
    width: 100%;
    height: 226px;
  }
  .mine-details .mine-body .node-list {
    width: 100%;
  }
}
